import { CreaterComponent } from './creater/creater.component';
import { AppComponent } from './app.component';
import { BoardComponent } from './board/board.component';
import { NgModule } from '@angular/core';
import { Routes, RouterModule, ActivatedRoute } from '@angular/router';
import { PtreeSignComponent } from './ptree-sign/ptree-sign.component';
import { HomepageComponent } from './homepage/homepage.component';

const routes: Routes = [
  { path: 'login/:type', component: PtreeSignComponent },
  { path: 'board', component: BoardComponent },
  { path: 'home', component: HomepageComponent },
  { path: 'creater', component: CreaterComponent  },

  { path: '',
    redirectTo: '/login',
    pathMatch: 'full'
  },
   { path: '**', component: PtreeSignComponent }
];

@NgModule({
  imports: [  RouterModule.forRoot(routes,     { enableTracing: true })],
  exports: [ RouterModule],
})
export class AppRoutingModule { }



