import { FormzModule } from './forms.module';
import { MaterialModule } from './material.module';
import { BrowserModule} from '@angular/platform-browser';
import { NgModule } from '@angular/core';


import { AppRoutingModule } from './app-routing.module';
import { AppComponent } from './app.component';
import { PtreeSignComponent } from './ptree-sign/ptree-sign.component';
import { BoardComponent } from './board/board.component';
import { HomepageComponent } from './homepage/homepage.component';
import { HttpClientModule } from '@angular/common/http';
import { CreaterComponent } from './creater/creater.component';
import { IntroComponent } from './intro/intro.component';
import { AccountService } from './account.service';


@NgModule({
  declarations: [
    AppComponent,
    PtreeSignComponent,
    BoardComponent,
    HomepageComponent,
    CreaterComponent,
    IntroComponent,
  ],
  imports: [
    BrowserModule,
    AppRoutingModule, MaterialModule, FormzModule, HttpClientModule,
  ],
  providers: [AccountService],
  bootstrap: [AppComponent],
})
export class AppModule { }



