import { HttpClient, HttpResponse } from '@angular/common/http';
import { AccountService } from './../account.service';
import { DataModel } from './../data-model';
import { Component, OnInit } from '@angular/core';
import {FormBuilder, FormControl, FormGroup, Validators} from '@angular/forms';

@Component({
  selector: 'app-ptree-sign',
  templateUrl: './ptree-sign.component.html',
  styleUrls: ['./ptree-sign.component.css']
})
export class PtreeSignComponent implements OnInit {

  public array1: any;
  gm = 'ayush';
  signup: FormGroup;
  signin: FormGroup;
  formlog: string;

  dataModel: DataModel;

  public sgnup = new FormControl();
  constructor(private fb: FormBuilder, private http: HttpClient, private accService: AccountService) {
    this.signup = this.fb.group({
      name: ['', Validators.required],
      uname: ['', Validators.required, Validators.minLength(5)],
      email: ['', Validators.required ],
      passwd: ['', Validators.required, Validators.minLength(8)],
    });
    this.signin = this.fb.group({
      uname: [''],
      passwd: ['']
    });
   }

  ngOnInit() {
    this.accService.accounts().subscribe(data => this.array1 = data);
  }
  onsubmit() {
    console.log(this.signup.status);
  }

}



