import {NgModule} from '@angular/core';
import {CommonModule } from '@angular/common';

import {BrowserAnimationsModule} from '@angular/platform-browser/animations';
import {MatButtonModule, MatCheckboxModule , MatProgressBarModule } from '@angular/material';
import {MatGridListModule, MatCardModule, MatInputModule} from '@angular/material';
import {MatMenuModule } from '@angular/material/menu';
import {MatIconModule} from '@angular/material/icon';
import {MatChipsModule} from '@angular/material';


@NgModule(
{
    imports: [BrowserAnimationsModule, MatButtonModule, MatCheckboxModule, MatProgressBarModule,
            MatMenuModule, MatIconModule, MatChipsModule, MatGridListModule, MatCardModule,
            MatInputModule,
            ],
    exports: [BrowserAnimationsModule, MatButtonModule, MatCheckboxModule, MatProgressBarModule,
            MatMenuModule, MatIconModule, MatChipsModule, MatGridListModule, MatCardModule,
            MatInputModule,
        ]
})
export class MaterialModule {}



